.btn {
    padding: 9px 18px;
    font-size: 14px;
    border: 3px solid transparent;
    background-color: #FF6E30;
    font-family: 'Roboto';
    color: #FFFFFF;
}

.btn-about {
    background-color: transparent;
    border: 3px solid #FF6E30;
}

.btn-yellow:hover {
    background-color: #BC572B;
}

.btn-yellow:focus {
    border-color: #CE4307;
}

.btn-yellow:active {
    background-color: #CE4307;
}

.btn-about:hover {
    background-color: #FF6E30;
}

.btn-about:focus {
    background-color: #BC572B;
}

.btn-about:active {
    background-color: #CE4307;
    border-color: #CE4307;
}

@media (min-width: 768px) {
    .btn {
        width: inherit;
        padding: 15px 20px;
        font-size: 16px;
    }
}