.header-top {
    padding: 24px;
    background-color: #202020;
    display: flex;
    justify-content: space-between;
}

.header-bottom-more__list-top {
    display: none;
}

.header-top-search {
    width: 24px;
    background-image: url('../img/loop.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.header-bottom {
    padding: 16px 24px;
    background-color: #4A4A4A;
}

.header-bottom-more {
    width: 100%;
}

.nav__burger {
    background-color: transparent;
    width: 30px;
    height: 30px;
}

.x {
    background-image: url('../img/X.svg');
    background-position: center center;
    background-repeat: no-repeat;
    border: none;
    background-color: transparent;
    width: 30px;
    height: 32px;
    transition: fill .25s ease-in-out;
    transition: border-color .25s ease-in-out;
}

.x svg {
    display: none;
}

.nav__burger:hover>svg * {
    fill: #FF6E30;
}

.nav__burger:focus>svg * {
    fill: #BC572B;
}

.nav__burger:active>svg * {
    fill: #CE4307;
}

.header-bottom-more__list {
    display: none;
}

.flex {
    display: flex;
    flex-direction: column;
}

.header-bottom-more__item {
    width: fit-content;
    font-size: 16px;
    color: #FFFFFF;
}

.flex .header-bottom-more__item {
    margin-top: 24px;
}

.header-bottom-more__item:hover a {
    color: #FF6E30;
}

.header-bottom-more__item:focus a {
    background-color: #BC572B;
    color: #FFFFFF;
}

.header-bottom-more__item:active a {
    color: #CE4307;
}

@media(min-width:768px) {
    .header-top img {
        height: 26px;
    }
    .nav__burger {
        display: none;
    }
    .header-bottom-more__list {
        display: flex;
        justify-content: space-between;
    }
}

@media(min-width: 1024px) {
    .header-bottom-more__list-top {
        display: flex;
        align-items: center;
        width: 70%;
        justify-content: space-between;
    }
    .header-bottom {
        display: none;
    }
}

@media (min-width: 1280px) {
    .header-top {
        padding: 22px 100px;
    }
}

@media (min-width: 1440px) {
    .header-top {
        padding: 22px 74px;
    }
}

@media (min-width: 1768px) {
    .header-top {
        padding: 22px 244px;
    }
}

@media (min-width: 1920px) {
    .header-top {
        padding: 22px 315px;
    }
}